.floating-label {
  position: relative;
  display: flex;
  flex-flow: column-reverse;
  label, input {
    transition: all 0.2s;
  }

  label {
    position: absolute;
  }

  input:placeholder-shown ~ label {
    transform-origin: left bottom;
    transform: translate(0, 2.125rem) scale(1.5);
  }
}
